import { useState } from "react";
import Applications from "./Applications";
import JoiningCode from "./JoiningCode";
import style from "./JoiningForm.module.css";
import Options from "./Options";
import ApplicationDetails from "./ApplicationDetails";
import HostelApplyApplication from "../HostelJoiningForm/HostelApplyApplication";
import HostelApplicationDetail from "../HostelJoiningForm/HostelApplicationDetail";

function JoiningForm({ applicantUserName, instituteId, hostelId }) {
  const [index, setIndex] = useState(1);
  const [join, setJoin] = useState("");
  const [otherData, setOtherData] = useState(null);
  return (
    <div className={style.joiningForm}>
      {index === 1 && <Applications setIndex={setIndex} />}
      {index === 2 && <Options setIndex={setIndex} setJoin={setJoin} />}
      {index === 3 && (
        <JoiningCode join={join} applicantUserName={applicantUserName} />
      )}
      {index === 5 && (
        <ApplicationDetails
          setIndex={setIndex}
          applicantUserName={applicantUserName}
        />
      )}
      {index === 6 && (
        <HostelApplyApplication
          setIndex={setIndex}
          instituteId={instituteId}
          setOtherData={setOtherData}
        />
      )}
      {index === 7 && (
        <HostelApplicationDetail
          setIndex={setIndex}
          preview={otherData?._id}
          hostelId={hostelId}
        />
      )}
    </div>
  );
}

export default JoiningForm;
