import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { admissionChangeAction } from "../redux/store/admission-slice";
import style from "./JoiningForm.module.css";
import { useInstituteAllApplicationList } from "../optimized/Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import Loading from "../optimized/Loader/Loading";
import { assestsUtilityUrl } from "../optimized/Services/UrlConfig/AssestsBaseUrl";
import { Link, useParams } from "react-router-dom";

function Card({ setIndex, applyData }) {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const [step, setStep] = useState(false);

  const onStepClose = () => {
    setStep(false);
  };
  return (
    <>
      {applyData?.type ? (
        <>
          <div className={style.card} onClick={() => setStep((pre) => !pre)}>
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "flex-start",
                gap: "1rem",
              }}
            >
              <img
                src={`${assestsUtilityUrl}/Vector.svg`}
                alt="admission icon"
              />

              <div
                style={{
                  width: "100%",
                }}
              >
                <div
                  className={style.cardRow}
                  style={{
                    justifyContent: "flex-start",
                    gap: "1rem",
                    alignItems: "center",
                  }}
                >
                  <h6
                    style={{
                      marginBottom: "0",
                    }}
                  >
                    {applyData?.type ?? ""}
                  </h6>
                  <img
                    src={`${assestsUtilityUrl}/next.svg`}
                    alt="admission icon"
                    style={{
                      rotate: "90deg",
                      height: "1.3rem",
                    }}
                  />
                </div>
              </div>
            </div>
            {step ? (
              <>
                {applyData?.apps?.map((appD) => (
                  <div
                    onClick={() => {
                      dispatch(
                        admissionChangeAction.admissionQuery({
                          studentAppId: appD?._id,
                          studentAdminId: appD?.admissionAdmin?._id,
                        })
                      );
                      setIndex(5);
                    }}
                    style={{
                      marginTop: "0.7rem",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "flex-start",
                      gap: "1rem",
                      marginLeft: "2rem",
                      borderTop: "0.5px solid #cacaca",
                      paddingTop: "0.7rem",
                    }}
                  >
                    <img
                      src="/images/members/admission-application-icon.svg"
                      alt="admission icon"
                    />
                    <div
                      style={{
                        width: "100%",
                      }}
                    >
                      <div className={style.cardRow}>
                        <h6>{appD?.applicationName}</h6>

                        {appD?.application_type === "Direct Admisison" && (
                          <img
                            src="/images/members/application-hotspot-icon.svg"
                            alt="admission icon"
                          />
                        )}
                      </div>
                      <div className={style.cardRow}>
                        {/* <p>{appD?.applicationDepartment?.dName}</p> */}
                        {appD?.applicationEndDate && (
                          <p>{t("last_date_to_apply")}</p>
                        )}
                        {appD?.applicationEndDate ? (
                          <h6
                            style={{
                              marginBottom: "0",
                            }}
                          >
                            {appD?.applicationEndDate}
                          </h6>
                        ) : (
                          ""
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </>
            ) : null}
          </div>
        </>
      ) : (
        <div
          className={style.card}
          onClick={() => {
            dispatch(
              admissionChangeAction.admissionQuery({
                studentAppId: applyData?._id,
                studentAdminId: applyData?.admissionAdmin?._id,
              })
            );
            setIndex(5);
          }}
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "flex-start",
            gap: "1rem",
          }}
        >
          <img
            src="/images/members/admission-application-icon.svg"
            alt="admission icon"
          />
          <div
            style={{
              width: "100%",
            }}
          >
            <div className={style.cardRow}>
              <h6>{applyData?.applicationName}</h6>

              {applyData?.application_type === "Direct Admisison" && (
                <img
                  src="/images/members/application-hotspot-icon.svg"
                  alt="admission icon"
                />
              )}
            </div>
            <div className={style.cardRow}>
              {/* <p>{applyData?.applicationDepartment?.dName}</p> */}
              {applyData?.applicationEndDate && (
                <p>{t("last_date_to_apply")}</p>
              )}
              {applyData?.applicationEndDate ? (
                <h6
                  style={{
                    marginBottom: "0",
                  }}
                >
                  {applyData?.applicationEndDate}
                </h6>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
      )}
    </>
  );
}

function HostelApplyCard({ setIndex }) {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.card}
        onClick={() => setIndex(6)}
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-start",
          gap: "1rem",
        }}
      >
        <img
          src="/images/members/admission-application-icon.svg"
          alt="admission icon"
        />
        <div
          style={{
            width: "100%",
          }}
        >
          <div className={style.cardRow}>
            <h6>{t("hostel_application")}</h6>
          </div>
        </div>
      </div>
    </>
  );
}
function Applications({ setIndex }) {
  const { t } = useTranslation();
  const searchId = useSelector((state) => state.idChange.searchProfile);

  const { instituteAllApplicationList, instituteAllApplicationListLoading } =
    useInstituteAllApplicationList({
      data: {
        id: searchId?.id,
        page: 1,
        limit: 1000,
      },
      skip: !searchId?.id,
    });

  return (
    <div className={style.applications}>
      <div className={style.top}>{t("select_appropriate_application")}</div>
      <div className={style.content}>
        <div className={style.contentBottom}>
          <div className={style.cardsContainer}>
            <HostelApplyCard setIndex={setIndex} />
            {instituteAllApplicationList?.list?.map((newApp) => (
              <Card setIndex={setIndex} key={newApp?._id} applyData={newApp} />
            ))}
            {instituteAllApplicationListLoading && <Loading />}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Applications;
